<template>
  <div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell title=""></h-cell>
        <h-cell title="" value=""></h-cell>
      </h-cell-group>
    </div>
    <div class="demo-cell">
      <h-cell-group>
        <h-cell title="" value="" label=""></h-cell>
      </h-cell-group>
    </div>
  </div>
</template>

<script>
import cell from '../../../packages/cell';
import cellgroup from '../../../packages/cellgroup';

export default {
  components: {
    [cell.name]: cell,
    [cellgroup.name]: cellgroup
  }
}
</script>

<style lang="less">
  .demo-cell {
    margin-top: 20px;
  }
</style>



