<template>
  <div class="tab-demo">
    <h-tabs v-model="active">
      <h-tab v-for="n in 4" :key="n" :title="'title' + n">
        {{ n }}
      </h-tab>
    </h-tabs>
  </div>
</template>

<script>
import tab from '../../../packages/tab';
import tabs from '../../../packages/tabs';

export default {
  components: {
    [tab.name]: tab,
    [tabs.name]: tabs
  },
  data() {
    return {
      active: 1
    }
  }
}
</script>

<style lang="less">
  .tab-demo {
    .h-tab__pane {
      padding: 30px;
      background-color: #fff;
    }
  }
</style>



